<template>
  <div style="align-self: center; width: 1450px;margin: 10px auto 0 auto">
    <div class="parallel-alignment">
      <div style="border: solid;height: 670px;width: 78%" >
        <h3>服务器投影画面</h3>
        <iframe id="iFrame" style="height: 594px;width: 98%" :src="server_projection_url" allowfullscreen="allowfullscreen">
        </iframe>
      </div>
      <div class="current-test-info" style="border: solid;width:22%;height:670px">
        <h3>当前测试信息</h3>
        <div v-if="currentTestCase" style="text-align: left">
          <div><span style="font-size: 16px;font-weight: bold">-目标系统: </span>
            <span style="font-size: 15px">{{ currentTestCase.system.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-初始场景选取:</span>
            <span style="font-size: 15px">{{ currentTestCase.initialScenario.type  }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-场景测试时长:</span>
            <span style="font-size: 15px">{{ currentTestCase.duration}}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-环境要素:</span>
            <span style="font-size: 15px">{{ currentTestCase.environments.type}}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-静态障碍物:</span></div>
          <div><span style="font-size: 16px;font-weight: bold">--行人:</span>
            <span style="font-size: 15px">{{ currentTestCase.pedestrian.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">--路障:</span>
            <span style="font-size: 15px">{{ currentTestCase.roadblock.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-动态障碍物:</span></div>
          <div><span style="font-size: 16px;font-weight: bold">--机动车:</span>
            <span style="font-size: 15px">{{ currentTestCase.motorVehicles.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">--自行车:</span>
            <span style="font-size: 15px">{{ currentTestCase.bicycles.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-EGO:</span>
            <span style="font-size: 15px">{{ currentTestCase.ego.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-交通设施:</span>
            <span style="font-size: 15px">{{ currentTestCase.trafficFacilities.type }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-目标事故类型:</span>
            <span style="font-size: 15px">{{ currentTestCase.targetAccidents.type}}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-场景评估策略:</span>
            <span style="font-size: 15px">{{ currentTestCase.evaluationStrategies.type }}</span></div>
        </div>
        <h3>测试日志</h3>
        <div v-if="logInfo" style="text-align: left">
          <div><span style="font-size: 16px;font-weight: bold">-测试场景队列数量：</span>
            <span style="font-size: 15px">{{ logInfo.queueNum }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-已测试场景数量：</span>
            <span style="font-size: 15px">{{ logInfo.testedScenariosNum }}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-事故场景数量: </span>
            <span style="font-size: 15px">{{logInfo.accidentScenariosNum}}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-迭代次数: </span>
            <span style="font-size: 15px">{{logInfo.iterations}}</span></div>
          <div><span style="font-size: 16px;font-weight: bold">-测试时长: </span>
            <span style="font-size: 15px">{{logInfo.testDuration}} h</span></div>
        </div>
        <textarea id = "logFileText" v-model="logInfo.logFileText" readonly style="resize:none"></textarea>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'TestDetails',
  data () {
    return {
      server_projection_url: 'http://10.176.36.38:6080/vnc.html', // 服务器投影的url
      timer: '', // 用于定时刷新
      currentTestCase: '', // 当前正在进行的测试实例的信息，展示在当前测试信息中，由loadCurrentTestInfo加载
      logInfo: {
        logFileText: '',
        queueNum: '',
        testedScenariosNum: '',
        accidentScenariosNum: '',
        iterations: '',
        testDuration: ''
      } // 测试日志信息，展示在测试日志中，由loadLogInfo加载
    }
  },
  created: function () {
    console.log('进入测试详情页')
    this.myRefresh()
  },
  mounted: function () {
    this.timer = setInterval(this.myRefresh, 6000)
    console.log('测试详情页中的信息每6秒刷新一次')
    /*
      this.$nextTick(() => {
        const iframe = window.frames['iFrame']
        const handleLoad = () => {
          setTimeout(() => {
            console.log(iframe)
            console.log(iframe.contentWindow)
            const Do = (iframe.contentWindow.document)
            if (document.all) {
            // IE浏览器
            Do.getElementById('noVNC_connect_button').click()
          } else {
          // 其它浏览器
            let event = new Event('click', {'bubbles': true, 'cancelable': true})
            console.log(Do.getElementById('noVNC_connect_button'))
            Do.getElementById('noVNC_connect_button').dispatchEvent(event)
}
          }, 500)
        }
        iframe.addEventListener('load', handleLoad, true)
      })
      */
    },
  destroyed: function () {
    clearInterval(this.timer)
    console.log('停止页面刷新')
    console.log('离开测试详情页')
  },
  methods: {
    loadLogInfo () { // 加载测试日志信息
      let _this = this
      this.$axios.get('/testdetails/loadloginfo/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.logInfo = resp.data.result
        } else {
          _this.logInfo.logFileText = resp.data.message
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    loadCurrentTestInfo () { // 加载当前测试实例信息
      let _this = this
      this.$axios.get('/testcase/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.currentTestCase = resp.data.result
          // console.log(_this.currentTestCase)
        }
        let cur = _this.currentTestCase
        if (cur.pedestrian.type.includes('增减')) {
          cur.pedestrian.type = '(' + cur.minPedestrian + '-' + cur.maxPedestrian + ')' + cur.pedestrian.type
        }
        if (cur.roadblock.type.includes('增减')) {
          cur.roadblock.type = '(' + cur.minRoadblock + '-' + cur.maxRoadblock + ')' + cur.roadblock.type
        }
        if (cur.motorVehicles.type.includes('增减')) {
          cur.motorVehicles.type = '(' + cur.minMotorVehicles + '-' + cur.maxMotorVehicles + ')' + cur.motorVehicles.type
        }
        if (cur.bicycles.type.includes('增减')) {
          cur.bicycles.type = '(' + cur.minBicycles + '-' + cur.maxBicycles + ')' + cur.bicycles.type
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    myRefresh () {
      this.loadLogInfo()
      this.loadCurrentTestInfo()
    }
  }
}
/*
  setTimeout(function() {
    console.log('begin to click')
  let iframeDocument = document.getElementsByTagName('iframe')[0]
  iframeDocument.onload = function() {
  console.log(iframeDocument.contentDocument)
  console.log(iframeDocument.contentWindow.document.getElementById('noVNC_connect_button').innerHTML)
  }
  console.log(iframeDocument.contentDocument)
  console.log(document.getElementsByTagName('iframe')[0])
  console.log(iframeDocument.contentWindow.document.getElementById('noVNC_connect_button').innerHTML)
  if (document.all) {
   // IE浏览器
  iframeDocument.getElementById('noVNC_connect_button').click()
 } else {
 // 其它浏览器
  let event = new Event('click', {'bubbles': true, 'cancelable': true})
  console.log(iframeDocument.getElementById('noVNC_connect_button'))
  iframeDocument.getElementById('noVNC_connect_button').dispatchEvent(event)
}
}, 5000)
*/
</script>

<style scoped>
.parallel-alignment {
  display: flex;
  justify-content: center;
}
#logFileText {
  width:80%;
  height:106px;
  font-size: 10px;
}
</style>



